<template>
    <div class="main-wrap">
      <div class="nav-bar clear-fix">
        <el-breadcrumb separator=">" class="fl-l">
          <el-breadcrumb-item>商品</el-breadcrumb-item>
          <el-breadcrumb-item>商品快捷编辑</el-breadcrumb-item>
        </el-breadcrumb>
      </div>
        <div class="form">
            <div class="caption">
                <div class="caption-item item-tip">
                    <div class="caption-label txt-danger">注意:</div>
                    <div class="caption-title txt-danger">修改以下信息无需再进入审核和翻译，信息生效需要几分钟的缓存时间！</div>
                </div>
                <div class="caption-item">
                    <div class="caption-label">商品标题:</div>
                    <div class="caption-title">{{goodsInfo.common_goods.goods_name}} &nbsp;</div>
                </div>
                <div class="caption-item">
                    <div class="caption-label">商品编码:</div>
                    <div class="caption-title">{{goodsInfo.common_goods.goods_sn}} &nbsp;</div>
                </div>
                <div class="caption-item">
                    <div class="caption-label">商品到期时间:</div>
                    <div class="caption-title">{{goodsInfo.common_goods.sale_closing_date}} &nbsp;</div>
                </div>
                
            </div>

            <el-form ref="goodsForm" :model="goodsInfo" label-width="120px" class="shortcut-form" :rules="rules">
                <div class="form-item">
                    <div class="txt-title">商品基本信息</div>
                    <el-form-item label="申报品名(中文)" prop="common_goods.declare_name" :rules="[{ required: true, message: '请输入申报品名(中文)'},{pattern: /^(?![0-9a-zA-Z\s]+$)[\u4e00-\u9fa50-9a-zA-Z\s]{1,}$/, message: '申报品名（中文）只能为中文、中文+数字的组合、中文+英文的组合'}]" >
                        <el-input v-model="goodsInfo.common_goods.declare_name" size="small" maxlength="20" tabindex="22"></el-input>
                        <div class="txt-tip">该字段用于大陆海关清关使用，20个字符以内。</div>
                    </el-form-item>
                    <el-form-item label="商品货号" prop="common_goods.product_code"  >
                        <el-input v-model="goodsInfo.common_goods.product_code" size="small" ></el-input>
                    </el-form-item>
                    <el-form-item label="单位" prop="common_goods.goods_units_val" :rules="{ required: true, message: '该字段必填', trigger: 'blur' }">
                        <el-select v-model="goodsInfo.common_goods.goods_units_val" placeholder="单位" size="small" clearable>
                            <el-option :label="item.cn_name" :value="item.en_name" v-for="item in units" :key="item.en_name"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="原价" prop="common_goods.market_price" :rules="{ required: true, message: '该字段必填' }">
                        <el-input v-model.number="goodsInfo.common_goods.market_price" size="small" @blur="changePrice_o()"></el-input>
                    </el-form-item>
                    <el-form-item label="售价" prop="common_goods.shop_price" :rules="{ required: true, message: '该字段必填'}">
                        <el-input v-model.number="goodsInfo.common_goods.shop_price" size="small" @blur="changePrice_c()"></el-input>
                        <div class="txt-tip txt-danger tip-discount" ref="validate_discount" tabindex="22" v-show="validate_discount">当多个SKU售价不同时，售价必须>=最低售价！</div>
                        <div class="txt-tip">当多个SKU售价不同时，售价必须>=最低售价！</div>
                    </el-form-item>
                    
                    <el-form-item label="是否带电">
                        <el-switch :active-value="'1'" :inactive-value="'0'" v-model="goodsInfo.common_goods.is_elec" active-color="#13ce66" inactive-color="#ff4949"></el-switch>
                    </el-form-item>
                    <el-form-item label="MSDS有效时间">
                        <el-date-picker v-model="goodsInfo.common_goods.is_msds" type="date" placeholder="北京时间" :pickerOptions="pickerOptions" size="small"></el-date-picker>
                        <div class="txt-tip txt-danger" v-show="validate_date" ref="validate_date" tabindex="22">如果产品带电，MSDS的有效时间必须填写</div>
                    </el-form-item>
                </div>
                <div class="form-item">
                    <div class="txt-title">包装信息及运费模板</div>
                    <el-form-item label="运费模板" prop="common_goods.freight_template_id" :rules="{ required: true, message: '请选择运费模板', trigger: 'blur' }">
                        <el-select v-model="goodsInfo.common_goods.freight_template_id" size="small" clearable >
                            <el-option :label="item.templateTitle" :value="item.id" v-for="item in freightList" :key="item.id" ></el-option>
                        </el-select>                       
                    </el-form-item>
                    <el-form-item label="商品重量(KG)" prop="common_goods.goods_weight" :rules="{ required: true, message: '该字段必填' }">
                         <el-input v-model.number="goodsInfo.common_goods.goods_weight" size="small" @blur="formatLength('goods_weight',3)" id="tab-common_goods.goods_weight"></el-input>                    
                    </el-form-item>
                    <el-form-item label="包装尺寸(CM)" class="inner el-form-item-required" prop="common_goods.package_length">
                        <el-input v-model.number="goodsInfo.common_goods.package_length" size="small" @blur="formatLength('package_length',0)"></el-input>
                        <span class="dot">X</span>
                        <el-input v-model.number="goodsInfo.common_goods.package_width" size="small" @blur="formatLength('package_width',0)"></el-input>
                        <span class="dot">X</span>
                        <el-input v-model.number="goodsInfo.common_goods.package_height" size="small" @blur="formatLength('package_height',0)"></el-input>
                        <div class="txt-tip txt-danger tip-size" ref="validate_size" tabindex="22" v-show="validate_size">商品包装的长宽高必填.</div>
                    </el-form-item>
                    
                </div>
                <div class="form-item">
                    <div class="txt-title">SKU信息</div>
                    <el-table :data="goodsInfo.product" class="report table-border" style="width: 100%" border>
                        <el-table-column type="index" width="80"></el-table-column>
                        <el-table-column prop="product_sn" label="SKU编码" min-width="120" show-overflow-tooltip></el-table-column>
                        <el-table-column v-if="type=='admin'" prop="product_id" label="SKU ID" min-width="120" show-overflow-tooltip></el-table-column>
                        <el-table-column prop="product_title" label="SKU货号" min-width="100" show-overflow-tooltip></el-table-column>

                        <el-table-column prop="attr_val_name" :label="'属性('+goodsInfo.attr_name+')'" min-width="200">
                        </el-table-column>
                        <el-table-column prop="product_price" label="增加价格(USD)" width="120">
                            <template slot-scope="scope">
                                <el-input v-model="scope.row.product_price" size="mini" @blur="numberFloat(scope)"></el-input>
                            </template>
                        </el-table-column>
                        <el-table-column  label="售价" width="120">
                            <template slot-scope="scope">
                                {{parseFloat(goodsInfo.common_goods.shop_price)}}
                            </template>
                        </el-table-column>
                        <el-table-column  label="调整后的价格" width="120">
                            <template slot-scope="scope">
                                 {{(parseFloat(scope.row.product_price)+parseFloat(goodsInfo.common_goods.shop_price))|toFixed}}
                            </template>
                        </el-table-column>
                        
                        <el-table-column prop="product_number" label="库存" width="180">
                            <template slot-scope="scope">
                                <el-input v-model.number="scope.row.product_number" size="mini" @blur="numberInt(scope)"></el-input>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>
                <div class="form-item item-image">
                    <div class="txt-title">商品图片 
                      <span class="tip">点击图片设置为主图,图片至少3张！</span>
                      <span class="reduction" @click="actionImg('reduction')">还原图片</span>
                    </div>
                    <div class="product-images clear-fix">          
                      <div class="fl-l images" v-for="(item,index) in goodsInfo.images" :key="index">
                        <div class="img" v-if="index==0">
                            <img :src="item.img_url" >
                            <div class="master">主图</div>
                        </div>
                        <div class="img" v-else >
                            <img :src="item.img_url"  @click="actionImg('master',index)">
                        </div>
                        <div class="action">
                          <i class="el-icon-caret-left" @click="actionImg('left',index)"></i>
                          <i class="el-icon-caret-right" @click="actionImg('right',index)"></i>
                          
                          <span @click="actionImg('delete',index)">删除</span>
                        </div>                           
                      </div>
                      

                    </div>
                </div>
                <div class="submit-box">
                    <el-button type="primary" @click="onSubmit" size="small"> 保 存 </el-button>
                    <el-button @click="jump" size="small"> 返 回 </el-button>
                </div>
            </el-form>
        </div>
    </div>
</template>
<script>
import country from "../../../json/country.json";
export default {
  data() {
    
    return {
      goods_id: "",
      type: "",
      supplierId:'',
      rules: {
        
      },
      validate_date: false,
      validate_discount: false,
      validate_size:false,
      //物流类型
      logisticsTypes: [],
      units: [],
      countries: country.data,
      goodsInfo: {
        product: [
          {
            product_id: "",
            goods_id: " ",
            product_sn: " ",
            product_number: " ",
            product_price: "",
            product_title: "",
            goods_attr_val: "",
            product_weight: "",
            goods_price: "",
            all_price: "",
            attr_val_name: ""
          }
        ],
        page_count: 0,
        record_count: "",
        attr_name: "",
        goods_name: " ",
        common_goods: {
          goods_sn: " ",
          goods_name: " ",
          shop_price: 0,
          market_price: 0,
          logistics_type: "",
          goods_units_val: "",
          product_code: "",
          goods_weight:'',
          package_length: "",
          package_width: " ",
          package_height: "",
          en_country: "",
          is_elec: "0",
          is_msds: "",
          goods_brand: "",
          freight_template_id:'',
          declare_name:''
        },
        
      },
      pickerOptions: {
        disabledDate(time) {
          if (time.getTime() < Date.now()) {
            return time;
          } else {
            return false;
          }
        }
      },
      
      //运费模板
      freightList:'',
      //运费模板是否可编辑
      freightEditable:true,
      submintData:{
        goodsGalleryIds:[]
      },
      images:[]
    };
  },
  filters: {
    toFixed(val) {
      return parseFloat(val) ? parseFloat(val).toFixed(2) : "计算中...";
    }
  },
  methods: {
    //获取物流类型
    getLogisticsType() {
      this.axios.post("/proxy/logistics/logistics/queryType", {body: { languageCode: "cn", status: 0 }}).then(response => {
        var data = response.data;
        if (data.head.code == "10000") {
          this.logisticsTypes = data.body.logisticsTypes;
        }
      });
    },
    // 获取商品信息
    getGoodsInfo() {
      this.axios.post("/proxy/fv/" + this.type + "/goods.php?act=get_product_list", { body: { goods_id: this.goods_id }}).then(response =>{
        let data = response.data;
        if (data.head.code == "10000") {
          this.images= JSON.parse(JSON.stringify(data.body)).images.concat();
          this.goodsInfo = data.body;
          this.goodsInfo.common_goods.is_msds = this.goodsInfo.common_goods.is_msds? new Date(this.goodsInfo.common_goods.is_msds).getTime(): ""; 
          
             
         
          //获取运费模板
          this.getfreightList();
        }
      });
    },
    //获取单位
    getUnit() {
      this.axios.post("/proxy/fv/" + this.type + "/goods.php?act=getGoodsUnits", {body: {}}).then(response => {
        let data = response.data;
        if(data.head.code=='10000'){
          this.units = data.body.gu_list;

        }
                
      });
    },

    //价格写入
    changePrice_o() {
      //原价修改
      if (parseFloat(this.goodsInfo.common_goods.market_price)) {
        this.goodsInfo.common_goods.market_price = parseFloat(
          this.goodsInfo.common_goods.market_price
        ).toFixed(2);
      } else {
        this.goodsInfo.common_goods.market_price = "";
      }
    },
    changePrice_c() {
      //售价修改
      if (parseFloat(this.goodsInfo.common_goods.shop_price)) {
        this.goodsInfo.common_goods.shop_price = parseFloat(
          this.goodsInfo.common_goods.shop_price
        ).toFixed(2);
      } else {
        this.goodsInfo.common_goods.shop_price = "";
      }
    },
    numberInt(scope) {
      let prop = scope.column.property;
      if (parseInt(scope.row[prop])) {
        scope.row[prop] = parseInt(scope.row[prop]);
      } else {
        scope.row[prop] = 0;
      }
    },
    numberFloat(scope) {
      let prop = scope.column.property;
      if (parseFloat(scope.row[prop])) {
        scope.row[prop] = parseFloat(scope.row[prop]).toFixed(2);
      } else {
        scope.row[prop] = 0;
      }
    },
    jump() {
       this.$router.push("/vendor/goods-list"); 
    },

    //包装 长宽高 数字校验
    formatLength(val,decimal) {
      if (!isNaN(parseFloat(this.goodsInfo.common_goods[val]))) {
        this.goodsInfo.common_goods[val] = Math.abs(parseFloat(this.goodsInfo.common_goods[val])).toFixed(decimal);
        if(val=='goods_weight'&&parseFloat(this.goodsInfo.common_goods[val])==0){
          this.goodsInfo.common_goods[val]='';
        }
      } else {
        this.goodsInfo.common_goods[val] = "";
      }
    },
  
    //获取运费模板
    getfreightList(){
        this.loading = true;
        let params={"pager":{"page":1,"pageSize":1000}};
        if(this.type=='admin'){
          this.supplierId=this.goodsInfo.common_goods.supplier_id;
          params.supplierId=this.supplierId;
        }
        this.axios.post("/proxy/freight/api/template/list", {body:params}).then(response => {
            this.loading = false;
            let data = response.data;
            if (data.head.code == "10000") {
                let tmpArr=JSON.parse(JSON.stringify(data.body.rows));
                let defaultFreight=[];     
                this.freightList=[];
                for(var i in tmpArr){
                    if(tmpArr[i].supplierId=='0'){
                        defaultFreight.push(tmpArr[i].id);
                    }else{
                        this.freightList.push(tmpArr[i]);
                    }
                }
                // 默认模板清空
                if(defaultFreight.includes(this.goodsInfo.common_goods.freight_template_id)){
                  this.goodsInfo.common_goods.freight_template_id='';
                }
            }
        });
    },
    //商品图片操作
    actionImg(type,index){
      window.clearTimeout(this.setTimeoutId); 
      this.setTimeoutId=setTimeout(()=>{
        switch(type){
          case 'left':
            if(index==0) break;
            let imageP=JSON.parse(JSON.stringify(this.goodsInfo.images[index-1]));
            this.goodsInfo.images.splice(index-1,1);
            this.goodsInfo.images.splice(index,0,imageP);          
          break;
          case 'right':
            if(index==(this.goodsInfo.images.length-1)) break;
            let imageN=JSON.parse(JSON.stringify(this.goodsInfo.images[index+1]));
            this.goodsInfo.images.splice(index+1,1);
            this.goodsInfo.images.splice(index,0,imageN);          
          break;
          case 'master':
            if(index==0) break;
            let image=JSON.parse(JSON.stringify(this.goodsInfo.images[index]));
            this.goodsInfo.images.splice(index,1);
            this.goodsInfo.images.unshift(image);       
          break;                
          case "delete":
            this.goodsInfo.images.splice(index,1);
          break;
          case "reduction" :  
            this.goodsInfo.images=this.images.concat([]);
            console.log(this.goodsInfo.images)
          break;
        }
      },300)

      
    },
  //提交表单
    onSubmit() {
      this.$refs.goodsForm.validate((validate,aa)=>{
        if(validate){
          //带电 日期验证
          if(this.goodsInfo.common_goods.is_elec == "1" &&!this.goodsInfo.common_goods.is_msds) {
            this.validate_date = true;
            setTimeout(() => {
              this.$refs["validate_date"].focus();
            }, 500);
            return false;
          } else {
            this.validate_date = false;
          }
          

          //商品价格校验
          let market_price=Number(this.goodsInfo.common_goods.market_price);
          let shop_price=Number(this.goodsInfo.common_goods.shop_price);
          if(!(market_price>shop_price)){
             this.$message.error('市场价格必须大于售价');
             return false;
          }

          if (this.goodsInfo.common_goods.is_msds) {
            this.goodsInfo.common_goods.is_msds = new Date(
              this.goodsInfo.common_goods.is_msds
            ).getTime();
          }
          //验证包装尺寸
          if(this.goodsInfo.common_goods.package_length===''||
            this.goodsInfo.common_goods.package_width===''||
            this.goodsInfo.common_goods.package_height==='' 
          ){
            this.validate_size = true;
            setTimeout(() => {
              this.$refs["validate_size"].focus();
            }, 500);
            return false;
          }else {
            this.validate_size = false;
          }
          let product_price_tmp=false;//sku价格必须要一个与售价一致
          let sku_flag=true;
          let sku_tip='';
          for(let i in this.goodsInfo.product){
            let it=this.goodsInfo.product[i];
            //“市场价"必须大于所有SKU售价中的最高价(基础价格+增量价格)
           
            if(!(market_price>(shop_price+Number(it.product_price)))){
                sku_flag=false;
                sku_tip='“市场价"必须大于所有SKU售价中的最高价(基础价格+增量价格)';                       
                break;
            }
            if(it.product_price==0){
                product_price_tmp=true;
            }         

          }
          if(!sku_flag){
            this.$message.error(sku_tip);
            return false
          }
          if(!product_price_tmp){
            this.$message.error('sku价格必须要有一个与售价一致！');
            return false
          }       
          //整理数据
          this.submintData.goodsId=this.goods_id;
          this.submintData.goodsLnameCn=this.goodsInfo.common_goods.declare_name;
          this.submintData.productCode=this.goodsInfo.common_goods.product_code;
          this.submintData.goodsWeight=(this.goodsInfo.common_goods.goods_weight*1000).toFixed(0);
          this.submintData.marketPrice=(this.goodsInfo.common_goods.market_price*100).toFixed(0);
          this.submintData.shopPrice=(this.goodsInfo.common_goods.shop_price*100).toFixed(0);
          this.submintData.isElec=this.goodsInfo.common_goods.is_elec=='1'?true:false;
          if(this.goodsInfo.common_goods.is_msds!=''){
              this.submintData.isMsds=this.format.asString('yyyy-MM-dd hh:mm:ss', new Date(this.goodsInfo.common_goods.is_msds));
          }    
          // this.submintData.enCountry=this.goodsInfo.common_goods.product_code;
          this.submintData.packageLength=(this.goodsInfo.common_goods.package_length*10).toFixed(0);
          this.submintData.packageWidth=(this.goodsInfo.common_goods.package_width*10).toFixed(0);
          this.submintData.packageHeight=(this.goodsInfo.common_goods.package_height*10).toFixed(0);
          this.submintData.freightTemplateId=this.goodsInfo.common_goods.freight_template_id;
          this.submintData.goodsUnitsVal=this.goodsInfo.common_goods.goods_units_val;
          let tempProduct=[];
          for(let i in this.goodsInfo.product){
            tempProduct.push({
                productId:this.goodsInfo.product[i].product_id,
                productNumber:this.goodsInfo.product[i].product_number,
                productPrice:this.goodsInfo.product[i].product_price*100
            })
          }
          this.submintData.products=tempProduct;
          this.submintData.goodsGalleryIds=[];
          for(let i in this.goodsInfo.images){
              this.submintData.goodsGalleryIds.push(this.goodsInfo.images[i].goods_gallery_id);
          }
          if(this.submintData.goodsGalleryIds.length<3){
            this.$message.error("商品图片至少3张！");
            return false;
          }
          if(parseFloat(this.goodsInfo.common_goods.goods_weight)>2){
              this.$confirm('商品重量超过2kg，请确认当前设置是否正确!', '提示', {
                  confirmButtonText: '确定',
                  cancelButtonText: '返回修改',
                  type: 'warning'
              }).then(() => {
                this.axios.post("/proxy/goods/api/syn/product", {body:this.submintData}).then(response=>{
                  let data = response.data;
                  if (data.head.code == "10000") {
                    this.$router.push("/vendor/goods-list");         
                  } else {
                    this.$message({
                      type: "error",
                      message: data.body.sub_msg
                    });
                  }
                });
              }).catch(() => {
                document.getElementById('tab-common_goods.goods_weight').focus();                           
            }); 
          }else{
              this.axios.post("/proxy/goods/api/syn/product", {body:this.submintData}).then(response=>{
                let data = response.data;
                if (data.head.code == "10000") {
                  this.$router.push("/vendor/goods-list");         
                } else {
                  this.$message({
                    type: "error",
                    message: data.body.sub_msg
                  });
                }
              });            
          }
  

          return false;
        }
      })

   
    },
     
  },
  created() {
    this.type = this.$route.params.type == "s" ? "supplier" : "admin";
    this.goods_id = this.$route.params.id;
    // this.getLogisticsType();
    this.getUnit();
    this.getGoodsInfo();

   
  }
};
</script>

<style lang="less" scoped>
.caption-item {
  padding-left: 100px;
  position: relative;
  padding-bottom: 15px;
  font-size: 14px;
  display: inline-block;
  &.item-tip {
    display: block;
  }
  .caption-label {
    position: absolute;
    top: 0;
    left: 0;
    text-align: right;
    width: 88px;
    padding-right: 12px;
  }
  .caption-title {
    font-weight: bolder;
    font-size: 12px;
  }
}

.shortcut-form {
  .form-item {
    .txt-title {
      font-size: 14px;
      color: #222;
      font-weight: bolder;
      span{
        font-weight: normal;
      }
      .tip{
        color:#E6A23C;
        margin: 0 10px;
      }
      .reduction{
        color:#409EFF;
        cursor: pointer;
      }
    }
    .txt-tip {
      font-size: 10px;
      color: #5a5a5a;
      line-height: normal;
      &:focus {
        outline: none;
      }
      
    }
    padding: 12px 12px 25px 10px;
    margin-bottom: 15px;
    border: 1px solid #919191;
    .txt-title {
      margin-bottom: 15px;
    }
    .el-form-item__content {
      width: 40%;
      padding-top: 2px;
    }
  }
  .item-image{
   
    text-align: left;
    .images{
      margin-right: 20px;
      text-align: center;
      margin-bottom:15px;
      .img{
        display: inline-block;
        margin-bottom: 10px;
        overflow: hidden;
        position: relative;
        .master{
          position: absolute;
          right: -40px;
          top: -40px;
          background-color: #67C23A;
          color: #fff;
          text-align: center;
          -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
          width: 80px;
          height: 80px;
          display: inline-block;  
          padding-top: 1px;
          box-sizing: border-box;
          padding-top: 56px;
          letter-spacing: 2px;
          font-weight: bold;
        }
        img{
          width: 155px;
          height: 155px;
          border-radius: 8px; 
          overflow: hidden;
          display: block; 
          box-sizing: border-box;
          border:1px solid #ccc;
          cursor: pointer;
        }
      }
      .action{
        i,span{
          color: #409EFF;
          font-size: 20px;
          cursor: pointer;
          display: inline-block;
          vertical-align: middle;
        }
        span{
          font-size: 14px;
          margin:0 5px;
        }
      }

    }
    .zhutu{
      font-size: 16px;
      font-weight: bold;
      color:#f04134;
    }
  }
  .submit-box{
    text-align: center;
    padding-bottom: 50px;
    padding-top: 20px;
  }
}

.txt-danger {
  color: #f04134 !important;
}
</style>
<style lang="less">
.shortcut-form {
  .form-item {
    .el-form-item__content {
      width: 40%;
    }
    .el-form-item {
      margin-bottom: 10px;
    }
  }
  .inner {
    .el-form-item__content {
      .dot {
        font-size: 12px;
        margin: 0 10px;
      }
      width: 80%;
      .el-input {
        width: 15%;
      }
    }
  }
  .el-form-item__error {
    width: 200%;
    padding-top: 0px;
    padding-left: 0;
  }
  .el-form-item-required .el-form-item__label {
    &::before{
          content: '*';
    color: #ff4949;
    margin-right: 4px;
    }
  }
}
</style>





